.opinionsBox {
  z-index: 3;
  width: 100%;
  height: 70vw;
  position: relative;
  box-shadow: 0px 4px 10px rgba(47, 47, 47, 0.25);
  background: linear-gradient(180deg, #1c1c1c 0%, #1c1c1c 100%);
}
.opinionsCircles {
  width: 6.5vw;
  height: 6.5vw;
  margin-top: -3vw;
  margin-left: 12vw;
  position: absolute;
  border-radius: 15rem;
  background: linear-gradient(228.48deg, #f2ec3b 20.3%, #80f9c0 96.91%);
  box-shadow: inset -10px -10px 40px rgba(255, 255, 255, 0.45),
    inset 8px 8px 25px rgba(0, 0, 0, 0.35);
}
.opinionShirt {
  top: 0.6vw;
  left: 2vw;
  width: 30vw;
  position: absolute;
}
.bigBlackContainer {
  right: 0;
  padding: 2vw;
  overflow: hidden;
  margin-top: -6vw;
  position: absolute;
}
.bigBlackCircle {
  width: 45vw;
  height: 45vw;
  margin-right: -13vw;
  border-radius: 50rem;
  background: #1c1c1c;
  box-shadow: -10px -10px 30px rgba(78, 78, 78, 0.25),
    15px 15px 30px rgba(0, 0, 0, 0.6);
}
.quotes {
  left: 50vw;
  width: 30vw;
  z-index: 2;
  margin-top: 30vw;
  position: absolute;
  transform: translate(-50%, -50%);
}
.smallBlackCircle {
  left: 15vw;
  z-index: 3;
  width: 11vw;
  height: 11vw;
  margin-top: 27vw;
  position: absolute;
  border-radius: 50rem;
  background: #1c1c1c;
  box-shadow: -6px -6px 20px rgba(78, 78, 78, 0.15),
    8px 8px 20px rgba(0, 0, 0, 0.85);
}
.mobileCover {
  z-index: 2;
  width: 20vw;
  margin-top: 28vw;
  margin-left: 5vw;
  position: relative;
}
.bookCover {
  left: 4vw;
  width: 25vw;
  margin-top: 41vw;
  position: absolute;
}
.opinionsData {
  right: 2vw;
  z-index: 3;
  width: 60vw;
  margin-top: 12vw;
  text-align: right;
  position: absolute;
  overflow-y: scroll;
}
.opinionsDataRow {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: center;
}
.opinionDataCenter {
  width: 85%;
}
.Opinions {
  color: white;
  font-size: 6vw;
  margin-bottom: 3vw;
  font-weight: bolder;
}
.OpinionDate {
  margin: 0;
  color: white;
  font-size: 2.3vw;
  font-family: Raleway;
}
.OpinionSubHead {
  margin: 0;
  color: #a4a4a4;
  font-size: 1.5vw;
  font-family: Raleway;
}
.opinionsDataPara {
  color: white;
  margin-top: 2vw;
  font-size: 1.2vw;
  font-family: Raleway;
}
.SideArrow1 {
  left: 10%;
  width: 2vw;
  cursor: pointer;
  position: absolute;
  transform: translate(-50%);
}
.SideArrow2 {
  right: 5%;
  width: 2vw;
  cursor: pointer;
  position: absolute;
  transform: translate(-50%);
}
.opinionsDataBox {
  width: 70%;
}
.girlsRow {
  display: flex;
  justify-content: space-around;
}
.girls {
  width: 20vw;
  height: 16.5vw;
  object-fit: cover;
  border-radius: 10px;
}
.opinionsData::-webkit-scrollbar {
  width: 1px;
}
.opinionsData::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0);
}

@media screen and (max-width: 760px) {
  .opinionsDataPara {
    color: white;
    font-size: 2vw;
    margin-top: 2vw;
    font-family: Raleway;
  }
  .opinionsData {
    right: 2vw;
    z-index: 3;
    width: 70vw;
    margin-top: 5vw;
    text-align: right;
    position: absolute;
  }
}

@media screen and (max-width: 760px) {
  .bigBlackContainer {
    right: 0;
    padding: 7vw;
    overflow: hidden;
    margin-top: -13vw;
    position: absolute;
  }
  .opinionsData {
    right: 1vw;
    z-index: 3;
    width: 75vw;
    margin-top: 2vw;
    text-align: right;
    position: absolute;
  }
  .opinionsBox {
    z-index: 3;
    width: 100%;
    height: 85vw;
    position: relative;
    box-shadow: 0px 4px 10px rgba(47, 47, 47, 0.25);
    background: linear-gradient(180deg, #1c1c1c 0%, #1c1c1c 100%);
  }
  .opinionsData {
    right: 1vw;
    z-index: 3;
    width: 75vw;
    margin-top: 10vw;
    text-align: right;
    position: absolute;
  }
  .opinionsCircles {
    width: 8vw;
    height: 8vw;
    margin-left: 6vw;
    margin-top: -3vw;
    position: absolute;
    border-radius: 15rem;
    background: linear-gradient(228.48deg, #f2ec3b 20.3%, #80f9c0 96.91%);
    box-shadow: inset -10px -10px 40px rgba(255, 255, 255, 0.45),
      inset 8px 8px 25px rgba(0, 0, 0, 0.35);
  }
  .opinionShirt {
    top: 1.5vw;
    left: -5vw;
    width: 35vw;
    position: absolute;
  }
  .smallBlackCircle {
    left: 10vw;
    z-index: 3;
    width: 14vw;
    height: 14vw;
    margin-top: 29vw;
    position: absolute;
    border-radius: 50rem;
    background: #1c1c1c;
    box-shadow: -6px -6px 20px rgba(78, 78, 78, 0.15),
      8px 8px 20px rgba(0, 0, 0, 0.85);
  }
  .mobileCover {
    z-index: 2;
    width: 25vw;
    margin-left: 0;
    margin-top: 32vw;
    position: relative;
  }
  .bookCover {
    left: 0;
    width: 30vw;
    margin-top: 50vw;
    position: absolute;
  }
}

@media screen and (max-width: 480px) {
  .OpinionDate {
    margin: 0;
    color: white;
    font-size: 3vw;
    font-family: Raleway;
  }
  .OpinionSubHead {
    margin: 5px 0 0 0;
    color: #a4a4a4;
    font-size: 2.8vw;
    font-family: Raleway;
  }
  .opinionsDataPara {
    color: white;
    font-size: 2.3vw;
    margin-top: 2vw;
    font-family: Raleway;
  }
  .opinionsData {
    right: 0vw;
    z-index: 3;
    width: 80vw;
    margin-top: 5vw;
    text-align: right;
    position: absolute;
  }
  .girls {
    width: 25vw;
    height: 25vw;
    object-fit: cover;
    border-radius: 10px;
  }
  .SideArrow1 {
    left: 11%;
    width: 4vw;
    cursor: pointer;
    position: absolute;
    transform: translate(-50%);
  }
  .SideArrow2 {
    right: 5%;
    width: 4vw;
    cursor: pointer;
    position: absolute;
    transform: translate(-50%);
  }
}
